import {
  Flex,
  Accordion,
  AccordionItem,
  AccordionButton,
  AccordionIcon,
  AccordionPanel,
  Stack,
  Box,
  Text,
} from "@chakra-ui/react";
import { nanoid } from "@reduxjs/toolkit";
import moment from "moment";
import React, { FC } from "react";
import { useSelector } from "react-redux";
import { UserReducerProps } from "../../interfaces";
import { RootState } from "../../redux/store";
const StarRatingComponent = require("react-star-rating-component");

interface ProductReviewsProps {
  reviews: any;
}

const ProductReviews: FC<ProductReviewsProps> = ({ reviews }) => {
  const { user_data }: UserReducerProps = useSelector((state: RootState) => state.userReducer);
  return (
    <Flex width={"90%"} flexDir={"column"}>
      <Accordion defaultIndex={[3]} allowMultiple>
        <AccordionItem key={nanoid()} borderWidth={0} mb={5}>
          <h2>
            <AccordionButton _hover={{}} borderWidth={0} px={0}>
              <Box flex="1" textAlign="left">
                <Text fontSize={"20px"}>Reviews({reviews?.reviews?.length || 0})</Text>
              </Box>

              <StarRatingComponent
                name="rate2"
                editing={false}
                starColor="#000000"
                emptyStarColor="#999999"
                starCount={5}
                value={reviews?.total_rating || 0}
              />
              <AccordionIcon zIndex={-10} />
            </AccordionButton>
          </h2>
          <AccordionPanel px="0px">
            <Flex flexDir={"column"} align={"flex-start"}>
              {reviews?.reviews?.map((e: any) => (
                <Stack key={nanoid()} align={"flex-start"} mb="20px">
                  <Text fontSize={"16px"} fontWeight={400} mb="5px">
                    {e?.title}
                  </Text>
                  <Flex align="center">
                    <StarRatingComponent
                      name="rate2"
                      editing={false}
                      starColor="#000000"
                      emptyStarColor="#999999"
                      starCount={5}
                      value={e?.rating}
                    />
                    <Text ml="5px" fontSize={"10px"} color="#999999">
                      {user_data?.customer?.firstName} −
                    </Text>
                    <Text ml="5px" fontSize={"10px"} color="#999999">
                      {moment(e?.created_at).format("DD MMM YYYY")}
                    </Text>
                  </Flex>
                  <Text fontSize={"12px"}>{e?.review}</Text>
                </Stack>
              ))}
            </Flex>
          </AccordionPanel>
        </AccordionItem>
      </Accordion>
    </Flex>
  );
};

export default ProductReviews;
