import React from "react";
import {
  Accordion,
  AccordionButton,
  AccordionIcon,
  AccordionItem,
  AccordionPanel,
  Box,
  Center,
  Flex,
  Heading,
  Image,
  Link,
  Skeleton,
  Spacer,
  Spinner,
  Stack,
  Text,
  useDisclosure,
  useToast,
  VStack,
  SkeletonCircle,
} from "@chakra-ui/react";
import { lazy, Suspense, useEffect, useState } from "react";
import { nanoid } from "nanoid";
import Images_Icons from "../../constants/icons-images";
import { useDispatch, useSelector } from "react-redux";
import { useLocation, useNavigate, useParams } from "react-router-dom";
import { AuthReducerProps, ProductReducerProps, UserReducerProps } from "../../interfaces";
import { RootState } from "../../redux/store";
import { Dispatch } from "redux";
import moment from "moment";
import MeProtocolToast from "../../hooks/MeProtocolToast";
import { addToCart } from "../../redux/CartSlice";
import Lottie from "lottie-react";
import checkMark from "../../assets/json/checkMark.json";
import axios from "axios";
import { BASE_URL } from "../../config";
import { getProductReviews, getSingleProduct } from "../../redux/ProductsSlice";
import { moveLeft } from "../../redux/BountySlice";
import { GLOBAL_PREFIX } from "../../constants";
import LoadingOverlay from "../../components/LoadingOverlay";
import ProductReviews from "./ProductReviews";
import ProductWorksAndPhotos from "./ProductWorksAndPhotos";
import PointsRedeemModal from "./PointsRedeemModal";
import AddCollectionModal from "./AddCollectionModal";
import ProductSyncRewardModal from "./ProductSyncRewardModal";
import ConnectWalletAndLike from "./ConnectWalletAndLike";
import { useMeProtocol } from "me-protocol";
import { useUpdateUserMutation } from "../../redux/rtkQuery/api";
import { getUserInfo } from "../../redux/UserSlice";

const Footer = lazy(() => import("../../components/Footer"));
const NewDashboardNavbar = lazy(() => import("../../components/NewDashboardNavBar"));
const ModalComponent = lazy(() => import("../../components/ModalComponent"));
const MeProtocolButton = lazy(() => import("../../components/MeProtocolButton"));

const ProductDetails = (): JSX.Element => {
  const toast = useToast();

  let navigate = useNavigate();
  const { state }: any = useLocation();
  const dispatch = useDispatch<Dispatch>();
  const { id } = useParams();

  const { loading, setUpWallet } = useMeProtocol();

  const { token, session_id }: AuthReducerProps = useSelector(
    (state: RootState) => state.authReducer
  );
  const { user_data }: UserReducerProps = useSelector((state: RootState) => state.userReducer);

  const { reviews, product, product_loading }: ProductReducerProps = useSelector(
    (state: RootState) => state.productReducer
  );

  const { cartItems } = useSelector((state: RootState) => state.cartReducer);

  const { isOpen, onToggle, onClose } = useDisclosure();
  const { isOpen: redeemIsOpen, onOpen: redeemOnOpen, onClose: redeemOnClose } = useDisclosure();

  const { isOpen: isOpenModal, onOpen: onOpenModal, onClose: onCloseModal } = useDisclosure();

  const { isOpen: isOpen3, onOpen: onOpen3, onClose: onClose3 } = useDisclosure();

  const { root, imageStyle, logoStyle } = useStyles();
  const [isLoaded, setIsLoaded] = useState<boolean>(true);

  const [inCart, setInCart] = useState<boolean>(false);

  const [rewardsFetching, setRewardsFetching] = useState<boolean>(false);
  // const [userSyncedRewards, setUserSyncedRewards] = useState<Array<any>>([]);

  const {
    isOpen: syncSuccessIsOpen,
    onOpen: syncSuccessOnOpen,
    onClose: syncSuccessOnClose,
  } = useDisclosure();
  const [updateUser] = useUpdateUserMutation();

  useEffect(() => {
    if (product?.id) {
      // dispatch(getProductReviews({ token, offer_id: product?.id }));
    }
    // if (token && product) {
    //   GET_ALL_SYNCED_REWARDS();
    // }

    dispatch(
      getSingleProduct({
        token,
        session_id,
        offerCode: id,
      })
    );
    //@ts-ignore
    dispatch(moveLeft(true));
  }, []);

  // const availablePoints = userSyncedRewards?.filter(
  //   (e: any) => e?.reward?.rewardId === product?.reward?.rewardId
  // );

  useEffect(() => {
    const checkInCart = cartItems.find((item: any) => item.id === product?.id);
    if (!checkInCart) return;
    setInCart(true);
  }, [cartItems]);

  function hasOfferExpired() {
    if (product?.inventory < -1) {
      return true;
    } else {
      return false;
    }
    // let timeDiff = moment().diff(moment(product?.expiry_date), "seconds");
    // if (timeDiff > 0) {
    //   return true;
    // } else {
    //   return false;
    // }
  }

  const CREATE_WALLET = async () => {
    try {
      const wallet: any = await setUpWallet({
        persist: false,
      });

      if (wallet.publicAddress) {
        await axios
          .put(
            `${BASE_URL}/customer/setup-wallet-address`,
            { walletAddress: wallet.publicAddress },
            { headers: { Authorization: `Bearer ${token}` } }
          )
          .then(({ data }: any) => {
            if (token) dispatch(getUserInfo({ token }));
            MeProtocolToast(toast, "Wallet created successfully", 2000);
            navigate(`${GLOBAL_PREFIX}/subsequentRedeeming`, {
              state: { ...product },
            });
          })
          .catch((e: any | unknown) => {
            console.log(e, "UPDATeYUSERZRR");
          });
      }
    } catch (error) {
      console.log(error, "CREATE_WALLETERR");
    }
  };

  // console.log(product);

  return (
    <Flex flexDir={"column"}>
      {/* ----------------------------------------------------------------------NAV BAR------------------------------------------------------------ */}
      {/* @ts-ignore */}
      <Suspense fallback={<Spinner size="sm" />}>
        <NewDashboardNavbar />
      </Suspense>
      <Stack {...root} display="flex">
        <Flex flexDir={"row"}>
          {/* ----------------------------------------------------------------------LEFT SECTION------------------------------------------------------------ */}
          <Flex w={"50vw"} bg={"grey"} flexDir={"column"}>
            {/* @ts-ignore */}
            {product?.offerImages?.map((e: any) => (
              <Skeleton key={nanoid()} isLoaded={isLoaded} overflow={"hidden"}>
                <Image src={e?.url} objectFit="cover" {...imageStyle} mixBlendMode={"multiply"} />
              </Skeleton>
            ))}
          </Flex>

          {/* ----------------------------------------------------------------------RIGHT SECTION------------------------------------------------------------ */}
          <Flex w={"50vw"} flexDir={"column"} px={"80px"} py={"30px"}>
            <Flex w={"600px"} flexDir={"column"}>
              <SkeletonCircle isLoaded={!product_loading} w={"100px"} h={"100px"}>
                <Box
                  as="button"
                  onClick={() =>
                    navigate(`${GLOBAL_PREFIX}/dashboard/shop/${product?.brand?.id}`, {
                      state: {
                        id: product?.brand?.id,
                        slug: product?.brand?.slug,
                        brandName: product?.brandName,
                      },
                    })
                  }
                >
                  <Image
                    src={product?.brand?.logo}
                    objectFit="contain"
                    {...logoStyle}
                    fallbackSrc={Images_Icons.appLogoPlaceholder}
                    fallbackStrategy="beforeLoadOrError"
                    mixBlendMode={"multiply"}
                  />
                </Box>
              </SkeletonCircle>

              <Text color={"#D0D0D0"} fontSize="20px" mb={"20px"} textTransform={"uppercase"}>
                {product?.offerCode}
              </Text>

              <Skeleton isLoaded={!product_loading}>
                <Text lineHeight={"48px"} w={"80%"} fontSize="40px" textTransform={"uppercase"}>
                  {product?.name}
                </Text>
              </Skeleton>

              <ConnectWalletAndLike
                isOpen={isOpen}
                onClose={onClose}
                product={product}
                product_loading={product_loading}
                // availablePoints={availablePoints}
                onToggle={onToggle}
                onOpenModal={onOpenModal}
                onOpen3={onOpen3}
                rewardsFetching={rewardsFetching}
              />
            </Flex>

            {/* ----------------------------------------------------------------REVIEW SECTION---------------------------------------- */}
            {reviews?.reviews?.length > 0 && <ProductReviews reviews={reviews} />}

            {/* ----------------------------------------------------------------WHERE TO REDEEM------------------------------------------ */}
            {product?.brand?.online_stores || product?.brand?.physical_stores ? (
              <Flex width={"90%"} flexDir={"column"}>
                <Accordion defaultIndex={[3]} allowMultiple px={0}>
                  <AccordionItem key={nanoid()} borderWidth={0} mb={5}>
                    <h2>
                      <AccordionButton _hover={{}} borderWidth={0} px={0}>
                        <Box flex="1" textAlign="left">
                          <Text fontSize={"20px"}>Where to redeem</Text>
                        </Box>
                        <AccordionIcon zIndex={-10} />
                      </AccordionButton>
                    </h2>
                    <AccordionPanel px="0px">
                      {/*--------------------------- Online stores ---------------------------*/}
                      {product?.brand?.online_stores?.length > 0 && (
                        <Box>
                          <Text color={"#AEAEAE"}>Online stores</Text>
                          {product?.brand?.online_stores?.map((e: any, i: number) => (
                            <Flex
                              key={nanoid()}
                              bg={i % 2 === 0 ? "grey" : "white"}
                              p={"5px"}
                              my={"5px"}
                              h={"40px"}
                              align="center"
                            >
                              <Text>{JSON.parse(e)?.name}</Text>
                              <Spacer />
                              <Flex>
                                <Link
                                  textDecoration={"underline"}
                                  target="_blank"
                                  href={`${JSON.parse(e)?.url}`}
                                >
                                  {JSON.parse(e)?.url}
                                </Link>
                                <Flex w={"25px"} h={"25px"} ml={"5px"}>
                                  <Image src={Images_Icons.linkIcon} />
                                </Flex>
                              </Flex>
                            </Flex>
                          ))}
                        </Box>
                      )}

                      {/*--------------------------- Physical stores ---------------------------*/}
                      {product?.brand?.physical_stores?.length > 0 && (
                        <Box>
                          <Text color={"#AEAEAE"} mt={"30px"}>
                            Physical stores
                          </Text>
                          {product?.brand?.physical_stores?.map((e: any, i: number) => (
                            <Flex
                              key={nanoid()}
                              bg={i % 2 === 0 ? "grey" : "white"}
                              p={"5px"}
                              my={"5px"}
                              h={"40px"}
                              align="center"
                            >
                              <Text>{JSON.parse(e)?.name}</Text>
                              <Spacer />
                              <Flex>
                                <Link
                                  textDecoration={"underline"}
                                  target="_blank"
                                  href="https://google.com"
                                >
                                  130, Powell St, San Francisco, CA
                                </Link>
                              </Flex>
                            </Flex>
                          ))}
                        </Box>
                      )}
                    </AccordionPanel>
                  </AccordionItem>
                </Accordion>
              </Flex>
            ) : (
              <></>
            )}

            {/* -----------------------------------------------------------------------HOW IT WORKS & PHOTOS--------------------------------------------- */}
            {/* <ProductWorksAndPhotos /> */}

            <Flex justify={"space-between"}>
              <Center
                as={"button"}
                h={"80px"}
                bg={hasOfferExpired() ? "#999999" : "dark"}
                w={"48%"}
                color={"light"}
                alignSelf={"center"}
                top={"80vh"}
                disabled={hasOfferExpired() || loading}
                _hover={{ bg: "dark" }}
                borderRadius={"0px"}
                onClick={async () => {
                  //6th june 2023
                  if (!token) {
                    navigate(`${GLOBAL_PREFIX}/login`);
                    return;
                  }
                  if (user_data?.customer?.walletAddress) {
                    navigate(`${GLOBAL_PREFIX}/subsequentRedeeming`, {
                      state: { ...product },
                    });
                  } else {
                    await CREATE_WALLET();
                  }
                }}
              >
                {loading ? <Spinner /> : <>{hasOfferExpired() ? "Out of stock" : "Redeem now"}</>}
              </Center>
              <Center
                as={"button"}
                h={"80px"}
                bg={"dark"}
                w={"48%"}
                color={"light"}
                alignSelf={"center"}
                top={"80vh"}
                _hover={{ bg: "dark" }}
                borderRadius={"0px"}
                onClick={() => {
                  if (!inCart) {
                    MeProtocolToast(toast, "Added to cart", 2000);
                  }
                  dispatch(
                    addToCart({
                      ...product,
                      quantity: 1,
                    })
                  );
                }}
              >
                {inCart ? "Added to bag" : "Add to bag"}
              </Center>
            </Flex>
          </Flex>
        </Flex>

        {/* ----------------------------------------ADD COLLECTION MODAL-------------------------------------- */}
        <AddCollectionModal
          isOpenModal={isOpenModal}
          onCloseModal={onCloseModal}
          restFns={() => {
            if (!isOpen) {
              onToggle();
            }
          }}
        />

        {/* ----------------------------------------REDEEM POINTS NOW  MODAL-------------------------------------- */}
        <PointsRedeemModal
          product={product}
          restFns={() => {
            syncSuccessOnOpen();
            // GET_ALL_SYNCED_REWARDS();
          }}
          redeemOnOpen={redeemOnOpen}
          redeemOnClose={redeemOnClose}
          redeemIsOpen={redeemIsOpen}
        />

        {/* ----------------------------------------SYNC REWARD PROVIDE EMAIL AND CODE MODAL-------------------------------------- */}

        <ProductSyncRewardModal
          product={product}
          onClose3={onClose3}
          isOpen3={isOpen3}
          restFns={() => {
            // GET_ALL_SYNCED_REWARDS();
          }}
        />

        {/* ----------------------------------------SUCCESS MODAL-------------------------------------- */}
        <Suspense fallback={<Spinner size="sm" />}>
          <ModalComponent
            isOpen={syncSuccessIsOpen}
            onClose={syncSuccessOnClose}
            closeOnOverlayClick={true}
          >
            <Stack w="100%">
              <VStack>
                <Lottie
                  style={{ width: "150px", height: "150px" }}
                  animationData={checkMark}
                  loop={false}
                  autoplay
                />
                <Heading fontSize={"18px"} pb="10px">
                  Reward redeemed successfully
                </Heading>
                <Suspense fallback={<Spinner size="sm" />}>
                  <MeProtocolButton
                    name="Ok"
                    onClick={() => {
                      window.scrollTo({ top: 0, behavior: "smooth" });
                      // GET_ALL_SYNCED_REWARDS();
                      // setRedeemLoading(false);
                      syncSuccessOnClose();
                    }}
                  />
                </Suspense>
              </VStack>
            </Stack>
          </ModalComponent>
        </Suspense>
      </Stack>

      <Suspense fallback={<Spinner size="sm" />}>
        <Footer />
      </Suspense>

      {product_loading && <LoadingOverlay />}
    </Flex>
  );
};

export default ProductDetails;

const useStyles = () => {
  return {
    root: {
      w: "100%",
      mt: "108px",
    },
    nameStyles: {
      fontSize: ["20px", "20px"],
    },
    button: {
      w: {
        base: "18%",
        md: "6%",
      },
      color: "dark",
      borderWidth: 0.5,
      backgroundColor: "light",
      fontSize: "15px",
    },
    loadMore: {
      w: "152px",
      h: "42px",
      alignSelf: "center",
      py: "10px",
      borderRadius: "100px",
    },
    imageStyle: {
      w: "100%",
      // h: "1008px",
    },
    addToFavPopOver: {
      w: "15vw",
    },
    logoStyle: {
      w: "100px",
      h: "100px",
      borderColor: "lightGrey",
      borderWidth: "0.5px",
    },
  };
};
