import {
  Stack,
  Flex,
  Heading,
  Center,
  Icon,
  Spinner,
  Select,
  Input,
  HStack,
  PinInput,
  PinInputField,
  VStack,
  Image,
  Text,
  Box,
  useToast,
} from "@chakra-ui/react";
import { nanoid } from "@reduxjs/toolkit";
import Lottie from "lottie-react";
import React, { FC, Suspense, useState } from "react";
import { IoMdCheckmark } from "react-icons/io";
import MeProtocolButton from "../../components/MeProtocolButton";
import ModalComponent from "../../components/ModalComponent";
import Images_Icons from "../../constants/icons-images";
import checkMark from "../../assets/json/checkMark.json";
import MeProtocolToast from "../../hooks/MeProtocolToast";
import {
  useSyncBrandMutation,
  useVerifySyncOtpMutation,
} from "../../redux/rtkQuery/api";

interface ProductSyncRewardModalProps {
  product: any;
  onClose3: any;
  isOpen3: any;
  restFns: any;
}

const ProductSyncRewardModal: FC<ProductSyncRewardModalProps> = ({
  product,
  onClose3,
  isOpen3,
  restFns,
}) => {
  const { desc, search, head } = useStyles();

  const toast = useToast();

  const [syncEmail, setSyncEmail] = useState<string>("");
  const [emailCode, setEmailCode] = useState<string>("");
  const [syncModalPage, setSyncModalPage] = useState<number>(0);
  const [codeErrMsg, setCodeErrMsg] = useState<string>("");
  const [verifyEmailOtpSent, setVerifyEmailOtpSent] = useState<boolean>(false);
  const [verificationPhone, setVerificationPhone] = useState<string>("");
  const [verificationOption, setVerificationOption] = useState<string>("");
  const [brandSyncLoading, setBrandSyncLoading] = useState<boolean>(false);
  const [emailCodeLoading, setEmailCodeLoading] = useState<boolean>(false);

  const [syncBrand] = useSyncBrandMutation();
  const [verifySyncOtp] = useVerifySyncOtpMutation();

  async function SendEmailVerificationForSync() {
    if (!syncEmail.includes("@")) return;
    setEmailCodeLoading(true);
    await verifySyncOtp({
      rewardId: product?.reward?.id,
      emailCode,
    })
      .then((res: any) => {
        if (res.error) {
          setCodeErrMsg(res?.error?.data?.error);
          return;
        }
        if (res?.data?.synced) {
          MeProtocolToast(toast, "Brand connected successfully", 3000);
          setTimeout(() => {
            restFns();
            // GET_ALL_SYNCED_REWARDS();
            setEmailCode("");
            setSyncEmail("");
            setVerifyEmailOtpSent(false);
            setSyncModalPage(2);
          }, 300);
        }
      })
      .catch((e: any | unknown) => {
        console.log(e, "SendEmailVerificationForSyncERR");
      })
      .finally(() => {
        setEmailCodeLoading(false);
      });
  }
  async function SendEmailCodeForSync(): Promise<void> {
    if (!syncEmail.includes("@")) return;
    setBrandSyncLoading(true);
    await syncBrand({
      rewardId: product?.reward?.id,
      email: syncEmail,
    })
      .then((res: any) => {
        if (res?.data) {
          setVerifyEmailOtpSent(true);
          MeProtocolToast(toast, "OTP sent successfully", 3000);
        }
      })
      .catch((e: any | unknown) => {
        console.log(e, "ERR");
      })
      .finally(() => {
        setBrandSyncLoading(false);
      });
  }

  const SyncModalCases = () => {
    switch (syncModalPage) {
      case 0:
        return (
          <Stack>
            <Flex flexDir={"row"} justify="space-between">
              <Heading {...head}>Sync {product?.reward?.name} Rewards</Heading>
              <Center as="button" onClick={onClose3}>
                <Image w="30px" h="30px" src={Images_Icons.cancel_icon} />
              </Center>
            </Flex>
            <Flex
              flexDir={"row"}
              align={"center"}
              justify={"space-between"}
              py={"2%"}
            >
              <Center w="100px" h="100px" overflow={"hidden"}>
                <Image
                  borderRadius={"10px"}
                  objectFit="contain"
                  src={product?.reward?.rewardImage}
                />
                <Box
                  w="100px"
                  h="100px"
                  borderRadius={"100px"}
                  pos="absolute"
                  bg="#9999992D"
                />
              </Center>

              {/* <Avatar size={"xl"} /> */}
              <Text {...desc} noOfLines={4} w="100%" pl="3%">
                {product?.brand?.description}
              </Text>
            </Flex>
            <Heading {...head}>What you’ll get</Heading>
            <Flex
              flexDir={"column"}
              bg={"#f6f6f6"}
              p={"5%"}
              borderRadius={"15px"}
            >
              {[
                `Authorize Me app to access your reward balance with ${product?.brand?.brandName}`,
                `Me app will Read reward balance from ${product?.brand?.brandName}`,
                `Me app will monitor changes in reward balance `,
              ]?.map((e: any) => (
                <Flex key={nanoid()}>
                  <Center
                    bg={"#000000"}
                    w={"20px"}
                    h={"20px"}
                    borderRadius={"20px"}
                  >
                    <Icon as={IoMdCheckmark} color={"light"} />
                  </Center>
                  <Text {...desc} noOfLines={2} pl={"2%"} w={"100%"}>
                    {e}
                  </Text>
                </Flex>
              ))}
            </Flex>
            <Suspense fallback={<Spinner size="sm" />}>
              <MeProtocolButton
                isLoading={brandSyncLoading}
                onClick={() => setSyncModalPage(1)}
                // onClick={() => SyncMyReward(brandToSync)}
                name={`Connect with ${product?.reward?.rewardName || "reward"}`}
              />
            </Suspense>
          </Stack>
        );
      case 1:
        return (
          <Stack>
            <Flex flexDir={"row"} justify="space-between">
              <Heading {...head}>Verify identity </Heading>
              <Center
                as="button"
                onClick={() => {
                  setSyncModalPage(0);
                  onClose3();
                  setVerificationOption("");
                  setVerifyEmailOtpSent(false);
                  setCodeErrMsg("");
                }}
              >
                <Image src={Images_Icons.cancel_icon} w="30px" h="30px" />
              </Center>
            </Flex>
            <Text {...desc} w="100%" pt="1%">
              Please select your preferred means of identification from the
              available options below
            </Text>
            <Flex {...search}>
              <Select
                color={"dark"}
                onChange={({ target }: any) => {
                  setVerificationOption(target.value);
                }}
                value={verificationOption}
                variant="unstyled"
                placeholder="Select verification option"
                size={"md"}
                _placeholder={{ color: "#999999" }}
              >
                {["Email", "Phone number"]?.map((e) => (
                  <option key={nanoid()} value={e}>
                    {e}
                  </option>
                ))}
              </Select>
            </Flex>
            {verificationOption && (
              <Stack>
                {verificationOption === "Email" ? (
                  <Input
                    {...search}
                    value={syncEmail}
                    onChange={(e: any) => {
                      setSyncEmail(e.target.value);
                    }}
                    placeholder="Enter email address"
                  />
                ) : (
                  <Input
                    {...search}
                    value={verificationPhone}
                    onChange={(e: any) => {
                      setVerificationPhone(e.target.value);
                    }}
                    placeholder="Enter phone number"
                  />
                )}
              </Stack>
            )}
            {verifyEmailOtpSent && (
              <Box my={"3%"}>
                <Text fontSize={"10px"}>
                  Please provide the OTP sent to your inbox
                </Text>
                <HStack w={"100%"} h={"60px"}>
                  <PinInput
                    onComplete={(e) => {
                      setEmailCode(e);
                      setCodeErrMsg("");
                    }}
                    otp
                    placeholder={"*"}
                  >
                    {[1, 2, 3, 4, 5, 6]?.map(() => (
                      <PinInputField
                        key={nanoid()}
                        bg={"grey"}
                        borderRadius={10}
                        borderWidth={0}
                        w={"15%"}
                        h={"100%"}
                      />
                    ))}
                  </PinInput>
                </HStack>
              </Box>
            )}
            {codeErrMsg && (
              <Text {...desc} color={"red"}>
                {codeErrMsg}
              </Text>
            )}
            <Suspense fallback={<Spinner size="sm" />}>
              <MeProtocolButton
                isLoading={emailCodeLoading || brandSyncLoading}
                onClick={() => {
                  if (verifyEmailOtpSent) {
                    SendEmailVerificationForSync();
                    return;
                  }
                  SendEmailCodeForSync();
                }}
                isDisabled={!syncEmail.includes("@")}
                name={"Continue"}
              />
            </Suspense>
          </Stack>
        );
      case 2:
        return (
          <Stack w="100%">
            <VStack>
              <Lottie
                style={{ width: "150px", height: "150px" }}
                animationData={checkMark}
                loop={false}
                autoplay
              />
              <Heading fontSize={"18px"} pb="10px">
                Brand connected successfully
              </Heading>
              <Suspense fallback={<Spinner size="sm" />}>
                <MeProtocolButton
                  name="Ok"
                  onClick={() => {
                    // refetchSyncRewards();
                    setSyncModalPage(0);
                    onClose3();
                    setVerificationOption("");
                    setVerifyEmailOtpSent(false);
                    setCodeErrMsg("");
                    setTimeout(() => {
                      window.scrollTo({ top: 0, behavior: "smooth" });
                    }, 100);
                  }}
                />
              </Suspense>
            </VStack>
          </Stack>
        );
    }
  };

  return (
    <Suspense fallback={<Spinner size="sm" />}>
      <ModalComponent
        isOpen={isOpen3}
        onClose={onClose3}
        closeOnOverlayClick={false}
      >
        {SyncModalCases()}
      </ModalComponent>
    </Suspense>
  );
};

export default ProductSyncRewardModal;

const useStyles = () => {
  return {
    desc: {
      pb: "5px",
      fontSize: {
        base: "12px",
        md: "15px",
      },
      w: {
        base: "100%",
        md: "80%",
      },
    },
    search: {
      w: "100%",
      h: "40px",
      background: "#F6F6F6",
      borderRadius: "53px",
      justify: "flex-start",
      align: "center",
      pl: "2%",
      borderWidth: "0px",
      _placeholder: {
        color: "#999999",
      },
      _focus: {
        borderColor: "black",
      },
      mt: "1%",
      mb: "2%",
    },
    head: {
      fontSize: {
        base: "12px",
        md: "14px",
      },
    },
  };
};
