import {
  Spinner,
  Stack,
  Input,
  Spacer,
  Button,
  Center,
  Text,
} from "@chakra-ui/react";
import React, { FC, Suspense, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import ModalComponent from "../../components/ModalComponent";
import { getUserCollection } from "../../redux/UserSlice";
import { Dispatch } from "redux";
import { useCreateCollectionMutation } from "../../redux/rtkQuery/api";
import { AuthReducerProps } from "../../interfaces";
import { RootState } from "../../redux/store";

interface AddCollectionModalProps {
  isOpenModal: any;
  onCloseModal: any;
  restFns: any;
}

const AddCollectionModal: FC<AddCollectionModalProps> = ({
  isOpenModal,
  onCloseModal,
  restFns,
}) => {
  const dispatch = useDispatch<Dispatch>();

  const { token }: AuthReducerProps = useSelector(
    (state: RootState) => state.authReducer
  );

  const [collectionName, setCollectionName] = useState<string>("");
  const [createCollectionLoading, setCreateCollectionLoading] =
    useState<boolean>(false);

  const [createCollection] = useCreateCollectionMutation();

  async function AddCollection(): Promise<void> {
    if (!collectionName) return;
    setCreateCollectionLoading(true);
    await createCollection({ name: collectionName })
      .then(async ({ data }: any) => {
        if (data.status === 200) {
          await dispatch(getUserCollection({ token }));
          onCloseModal();
          restFns();
        }
      })
      .catch((e: any | unknown) => {
        // setCreateCollectionError(e);
      })
      .finally(() => {
        setCreateCollectionLoading(false);
        setCollectionName("");
      });
  }

  return (
    <Suspense fallback={<Spinner size="sm" />}>
      <ModalComponent
        isOpen={isOpenModal}
        onClose={onCloseModal}
        closeOnOverlayClick={false}
      >
        <Stack>
          <Text>Create new collection</Text>
          <Input
            variant="outline"
            my={"2vw"}
            h={"40px"}
            _placeholder={{ color: "#cfcfcf" }}
            borderRadius={0}
            placeholder="Collection name"
            value={collectionName}
            onChange={({ target }: { target: { value: string } }) => {
              setCollectionName(target.value);
            }}
          />
          <Spacer />
          <Button
            isLoading={createCollectionLoading}
            // as={Button}
            bg={"dark"}
            color={"white"}
            h={"40px"}
            borderRadius={0}
            _hover={{ bg: "dark" }}
            onClick={AddCollection}
            disabled={!collectionName}
          >
            <Text>Next</Text>
          </Button>
          <Center
            bg={"light"}
            color={"dark"}
            h={"40px"}
            borderWidth={1}
            mt={"2px"}
            onClick={onCloseModal}
            as="button"
          >
            <Text>Cancel</Text>
          </Center>
        </Stack>
      </ModalComponent>
    </Suspense>
  );
};

export default AddCollectionModal;
