import { Spinner, Stack, Heading, HStack, Button, Center, Text, Image } from "@chakra-ui/react";
import Lottie from "lottie-react";
import { FC, Suspense, useState } from "react";
import ModalComponent from "../../components/ModalComponent";
import Images_Icons from "../../constants/icons-images";
import spinning_loader from "../../assets/json/spinning_loader.json";
import axios from "axios";
import { BASE_URL } from "../../config";
import { useSelector } from "react-redux";
import { AuthReducerProps } from "../../interfaces";
import { RootState } from "../../redux/store";

interface PointsRedeemModalProps {
  redeemOnOpen: any;
  redeemOnClose: any;
  redeemIsOpen: any;
  product: any;
  restFns: any;
}

const PointsRedeemModal: FC<PointsRedeemModalProps> = ({
  redeemOnOpen,
  redeemIsOpen,
  redeemOnClose,
  product,
  restFns,
}) => {
  const { token }: AuthReducerProps = useSelector((state: RootState) => state.authReducer);
  const [redeemLoading, setRedeemLoading] = useState<boolean>(false);

  async function SIMULATE_REDEEM() {
    setRedeemLoading(true);
    try {
      setRedeemLoading(true);

      const { status }: any = await axios.post(
        `${BASE_URL}/redeem`,
        {
          offerId: product?.id,
          amount: product?.point_discount,
          quantity: 1,
        },
        { headers: { Authorization: `Bearer ${token}` } }
      );

      setTimeout(() => {
        if (status === 200) {
          restFns();
          redeemOnClose();
          setRedeemLoading(false);
        }
      }, 2000);
    } catch (e: any) {
      console.log(e, "SIMULATE_REDEEM_ERR");
      setRedeemLoading(false);
    } finally {
      // setRedeemLoading(false);
    }
  }

  return (
    <Suspense fallback={<Spinner size="sm" />}>
      <ModalComponent isOpen={redeemIsOpen} onClose={redeemOnClose} closeOnOverlayClick={false}>
        <Stack>
          <Heading fontSize={"14px"}>Redeem now</Heading>
          <Text pt="5px" pb="20px">
            You are about to be redirected to the official brand's website to complete your purchase
            process.
          </Text>
          <HStack w={"100%"} justifyContent={"center"}>
            <Image
              src={Images_Icons.appLogo}
              // objectFit={"cover"}
              w="50px"
              h="50px"
              borderRadius={"70px"}
              objectFit="contain"
              bg="#9999993B"
            />
            {redeemLoading ? (
              <Lottie
                style={{ width: "50px", height: "50px" }}
                animationData={spinning_loader}
                loop
                autoplay
              />
            ) : (
              <Text>-------------------connect-------------------</Text>
            )}
            {/* <Avatar size={"md"} bg={"grey"} src={product?.brand?.logo} /> */}
            <Image
              src={product?.brand?.logo}
              w="50px"
              h="50px"
              borderRadius={"70px"}
              objectFit="contain"
              bg="#9999993B"
            />
          </HStack>
          <Button
            isLoading={redeemLoading}
            // as={"a"}
            bg={"dark"}
            mt={"3%"}
            color={"white"}
            h={"40px"}
            borderRadius={0}
            _hover={{ bg: "dark" }}
            onClick={() => {
              if (product?.reward?.rewardType === "REGULARPOINTS") {
                SIMULATE_REDEEM();
              }
            }}
          >
            <Text>Continue</Text>
          </Button>
          <Center
            bg={"light"}
            color={"dark"}
            h={"40px"}
            borderWidth={1}
            mt={"2px"}
            onClick={() => {
              setRedeemLoading(false);
              redeemOnClose();
            }}
            as="button"
          >
            <Text>Cancel</Text>
          </Center>
        </Stack>
      </ModalComponent>
    </Suspense>
  );
};

export default PointsRedeemModal;
